@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
:root{--color-foreground: #5d00a5;--color-foreground-rgb: 93, 0, 165;--color-foreground-alternative: #330066;--color-background: #8f00cd;--color-background-rgb: 143, 0, 205;--font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;font-size:1.15rem;font-family:'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;font-family:var(--font-family);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none}h1,h2,h3,h4,h5,h6{line-height:2;font-weight:400;color:#5d00a5;color:var(--color-foreground)}h1{font-size:1.5rem}p{line-height:2}

